@import url('https://fonts.googleapis.com/css2?family=Lexend:wght@300;400;500;600;700;800&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans&display=swap');

body {
  margin: 0;
}

.fondo {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 844px;
  height: 390px;
  background-color: #17181f;
}

.casa {
  position: absolute;
  width: 579px;
  height: 390px;
  left: 133px;
  background-image: url('https://i.redd.it/5fc2npi6jsq11.png');
  background-size: cover;
  background-position-y: -111px;
}

.puerta {
  position: absolute;
  width: 84px;
  height: 125px;
  bottom: 8px;
  left: 232px;
}

header {
 position: absolute;
 display: flex;
 width: 800px;
 height: 73px;
 top: 34px;
 left: 23px;
 background-color: #314d4a;
 align-items: center;
 justify-content: center;
}

.Titulo {
  position: absolute;
  font-family: 'Lexend', sans-serif;
  font-size: 27px;
  text-align: center;
  color: #ebe68d;
}

.highlight {
  color: #ea2a15;
}

